<script>
  export let title;
  export let projectType;
  export let timeframe;
  export let summary;
  export let deliverables;
  export let link;
  export let images;
  export let previousProject;
  export let nextProject;

  const months = "January_February_March_April_May_June_July_August_September_October_November_December".split(
    "_"
  );
  const projectDateStarted = new Date(timeframe.start);
  const projectDateEnded = new Date(timeframe.end);
  const projectDateStartedInYear = projectDateStarted.getFullYear();
  const projectDateStartedInMonth = months[projectDateStarted.getMonth()];
  const projectDateEndedInYear = projectDateEnded.getFullYear();
  const projectDateEndedInMonth = months[projectDateEnded.getMonth()];

  const timeframes = () => {
    if (
      projectDateStartedInMonth === projectDateEndedInMonth &&
      projectDateStartedInYear === projectDateEndedInYear
    ) {
      return `${projectDateEndedInMonth} ${projectDateEndedInYear}`;
    }
    if (projectDateStartedInYear === projectDateEndedInYear) {
      return `${projectDateStartedInMonth} - ${projectDateEndedInMonth} ${projectDateEndedInYear}`;
    }

    return `${projectDateStartedInMonth} ${projectDateStartedInYear} - ${projectDateEndedInMonth} ${projectDateEndedInYear}`;
  };
</script>

<section class="flex flex-col-reverse lg:flex-row">

  <div class="flex flex-col mr-0 lg:mr-20" style="flex: 4">
    {#each images as image, index}
      <div class="mb-10">
        <div class="flex justify-between">
          <span class="text-sm font-body text-white">{image.title}</span>
          <span class="text-sm font-body text-white opacity-50 text-right">
            {index + 1} of {images.length}
          </span>
        </div>

        <hr class="border-white border-t-2 opacity-25 my-4" />

        <img
          class="w-full"
          srcset={`images/projects/${title.replace(/ /g, '%20')}/${image.title.replace(/ /g, '%20')}@3x.jpg 3x,
          images/projects/${title.replace(/ /g, '%20')}/${image.title.replace(/ /g, '%20')}@2x.jpg 2x`}
          src="images/projects/{title}/{image.title}.jpg"
          alt={image.title} />
      </div>
    {/each}
  </div>

  <div class="mb-16" style="flex: 3">
    <h3 class="mb-4 text-sm text-white font-body uppercase tracking-widest">
      {projectType}
    </h3>

    <h2 class="mb-10 text-4xl text-white font-display">{title}</h2>

    <div class="mb-8">
      <h4 class="mb-2 tex-sm text-white font-body opacity-50 uppercase">
        Timeframe
      </h4>
      <p class="text-base text-white font-body">{timeframes()}</p>
    </div>

    <div class="mb-8">
      <h4 class="mb-2 tex-sm text-white font-body opacity-50 uppercase">
        Summary
      </h4>
      <p class=" text-base text-white font-body">{summary}</p>
    </div>

    <div class="mb-10">
      <h4 class="mb-2 tex-sm text-white font-body opacity-50 uppercase">
        Deliverables
      </h4>
      <p class=" text-base text-white font-body">{deliverables.join(', ')}</p>
    </div>

    <a href={link} class="border-none text-base text-white font-body underline">
      Link to the project ↗
    </a>

    <div class="base-shadow border-white border-2 flex mt-48">

      {#if previousProject}
        <button class="flex flex-col items-end p-6 flex-1 border-none">
          <span
            class="text-sm font-body text-white text-right uppercase opacity-50
            mb-2">
            ← Prev
          </span>
          <span
            class="text-xl font-body text-white text-right truncate ..."
            style="max-width: 7.5rem">
            {previousProject.name}
          </span>
        </button>
      {/if}

      {#if previousProject && nextProject}
        <div class="flex bg-white opacity-25" style="width: 2px" />
      {/if}

      {#if nextProject}
        <button class="flex flex-col items-start p-6 flex-1 border-none">
          <span
            class="text-sm font-body text-white text-left uppercase opacity-50
            mb-2">
            Next →
          </span>
          <span
            class="text-xl font-body text-white text-left truncate ..."
            style="max-width: 7.5rem">
            {nextProject.name}
          </span>
        </button>
      {/if}

    </div>
  </div>

</section>
