<script>
  export let classes;

  import { getContext } from "svelte";
  import data from "./fixtures.json";
  import ProjectBox from "./components/ProjectBox.svelte";
  import ProjectDetailModal from "./ProjectDetailModal.svelte";

  const { open } = getContext("simple-modal");

  const showProjectModal = (project, index) => {
    const prevProject = data.projects[index - 1];
    const nextProject = data.projects[index + 1];

    open(ProjectDetailModal, {
      title: project.name,
      projectType: project.type,
      timeframe: project.timeframe,
      summary: project.summary,
      deliverables: project.deliverables,
      link: project.link,
      images: project.images,
      previousProject: prevProject,
      nextProject: nextProject
    });
  };
</script>

<style>
  .rotates-90 {
    transform: rotate(90deg);
  }
</style>

<section class={classes}>
  <div class="flex lg:hidden justify-center -mt-32 -mb-16">
    <img
      class="rotate-90 rotates-90"
      src="images/selected-works.svg"
      alt="selected works label" />
  </div>

  <div class="hidden lg:block absolute -mx-16">
    <img src="images/selected-works.svg" alt="selected works label" />
  </div>

  <div class="grid grid-cols-1 lg:grid-cols-2 gap-6">
    {#each data.projects as project, index}
      <ProjectBox
        title={project.name}
        href={project.link}
        projectType={project.type}
        overview={project.overview}
        onClick={() => showProjectModal(project, index)} />
    {/each}
  </div>

</section>
